 $(window).on('load', function () {
    setFlowBanner();  
})

function setFlowBanner(){
    const $roll = $('.rolling');
    const $list = $('.rolling .list');
    let rollWidth = $roll.width();
    let listWidth = $list.width();
    const speed = 60; //1Ï¥àÏóê Î™áÌîΩÏÖÄ Ïù¥ÎèôÌïòÎäîÏßÄ ÏÑ§Ï†ï

    //Î¶¨Ïä§Ìä∏ Î≥µÏ†ú
    let $clone = $list.clone();
    $roll.append($clone);
    flowBannerAct()

    //Î∞∞ÎÑà Ïã§Ìñâ Ìï®Ïàò
    function flowBannerAct() {
        //Î¨¥Ìïú Î∞òÎ≥µÏùÑ ÏúÑÌï¥ Î¶¨Ïä§Ìä∏Î•º Î≥µÏ†ú ÌõÑ Î∞∞ÎÑàÏóê Ï∂îÍ∞Ä
        if (listWidth < rollWidth) {
            const listCount = Math.ceil(rollWidth * 2 / listWidth);
            for (let i = 2; i < listCount; i++) {
                $clone = $clone.clone();
                $roll.append($clone);
            }
        }
        $roll.find('.list').css({ 'animation': `${listWidth / speed}s linear infinite flowRolling` }); 
    } 
}